import './button.css';
import React from 'react';

export interface ButtonProps {
    onClick: () => void;
    disabled?: boolean;
    className?: string;
    children: React.ReactNode;
    primary?: boolean;
    label?: string;
    size: 'small' | 'medium' | 'large';
    backgroundColor?: string;
    name: string;
    mode: string; // Change `any` to a specific type, e.g., 'light' | 'dark'
    style?: React.CSSProperties; 
    variant: 'primary' | 'success' | 'danger' | 'warning';
}

const Button = ({ onClick, disabled, children, size, mode, variant, style }: ButtonProps) => {
    const handleClick = () => {
        onClick(); 
        alert('Button clicked!');
    };

    return (
        <button
            style={{
                color: 'white',
                backgroundColor: '#072196dd',
                height: 30,
                width: 70,
                borderRadius: 10,
                ...style
            }}
            type="button"
            name="ButtonName"
            onClick={handleClick}
            disabled={disabled}
            className={['storybook-button', `storybook-button--${size}`, mode, `storybook-button--${variant}`].join(' ')}
        >
            {children}
        </button>
    );
};

export default Button;
